/*global kakao*/
import React, { useState, useEffect } from "react"; 
import axios from 'axios';
import MainComponent from './Main'
import { useLocation } from "react-router-dom";
import placeStore from '../../base/Header/search/placeStore';
import store from "../../../Store/useStore";
import refreshTokenStore from "../../../Store/refreshTokenStore";

const MainPage = () => {    
    
    // style
    const [state, setState] = useState({ 
        style: {
            width: "100%", 
            height: "90vh",
        },
        zoom: 15,
        center: {
            lat: 33.451475,
            lng: 126.570528,
        },
        windowPosition: {
            lat: 33.450701,
            lng: 126.570667,
        },
        errMsg: null,
        isLoading: true,
        isPanto: true,
    })


    //   
    useEffect(() => {
        if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
            (position) => { //
            setState((prev) => ({
                ...prev,
                center: {
                    lat: position.coords.latitude, 
                    lng: position.coords.longitude, 
                },
                windowPosition: {
                    lat: (position.coords.latitude + 0.007), 
                    lng: position.coords.longitude, 
                },
                isLoading: false,
            }))
            }, //
            (err) => {
            setState((prev) => ({
                ...prev,
                errMsg: err.message,
                isLoading: false,
            }))
            }
        )
        } else { //gelocation  
        setState((prev) => ({
            ...prev,
            errMsg: "geolocation  .",
            isLoading: false,
        }))
        }
    }, [])


    //  
    const location = useLocation();
    //zustand   
    const { searchPlace, searchPlaceReset } = placeStore(state=>state);  


    //kakao   Request 
    const KAKAO_KEY = "58e74138796d9b5bbef0b990d53ebe32"
    const Kakao = axios.create({
        baseURL: "https://dapi.kakao.com", //   
        headers: {
            Authorization: "KakaoAK " + KAKAO_KEY //  
        }
    });

    const kakaoImgSearch = params => {
        return Kakao.get("/v2/search/image", {params})
    };


    //    state
    const [info, setInfo] = useState();
    const [markers, setMarkers] = useState([])
    const [map, setMap] = useState();
    
    // searchPlace   useEffect 
    useEffect(() => {
        if(!map) return;
        const ps = new kakao.maps.services.Places();

        let callback = function(data, status, _pagination) {
            if(status === kakao.maps.services.Status.OK) {
                //        
                //LatLngBounds   
                const bounds = new kakao.maps.LatLngBounds();
                let markers = [];
        
                for (let i = 0; i < data.length; i++) {
                    markers.push({
                        position: { //
                            lat: data[i].y,
                            lng: data[i].x,
                        },
                        name: data[i].place_name, //
                        address: data[i].address_name, //
                    })
                    
                    //  
                    bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
                }
                setMarkers(markers);

        
                //       
                map.setBounds(bounds);


                //    
                async function getImgs() {
                    let image = [...markers];

                    //     
                    for(let i=0; i<markers.length; i++) {
                        try{
                            const params = {
                                query: markers[i].name, // 
                                size: 3, //  
                            };
                            const result = await kakaoImgSearch(params);
            
                            if(result) { //  request 
                                //    url push
                                for(let j=0; j<result.data.documents.length; j++) {
                                    if(j !== 0) { //   imageUrl  
                                        image[i].imageUrl.push(result.data.documents[j].image_url);
                                    } else { //  imageUrl  
                                        image[i].imageUrl = [result.data.documents[j].image_url];
                                    }
                                }
                                setMarkers(image);
                            } else { //
                                console.log("fail");
                            }
                        } catch(error) {
                            console.log("error", error);
                        }
                    }
                }
                getImgs();
                searchPlaceReset();
            
            } else if(status === kakao.maps.services.Status.ZERO_RESULT) {
                alert('   .');
                return;
            } else if(status ===  kakao.maps.services.Status.ERROR) {
                alert('    .');
                return;
            }
        }

        if(searchPlace !== '') {
            //''   options 
            ps.keywordSearch(searchPlace, callback, {category_group_code:'AD5'});
        }
        
    }, [searchPlace])


    //  , state     
    const user = store();
    const refresh = refreshTokenStore();
    const [mapInfo, setMapInfo] = useState();
    const [markerInfo, setMarkerInfo] = useState();
    useEffect(() => {
        handleMapInfo();
        console.log(mapInfo);
    },[map]) 

    const handleMapInfo = () => {
        map && (setMapInfo({
            level: map.getLevel(),
            swLatLng: {
              lat: map.getBounds().getSouthWest().getLat(),
              lng: map.getBounds().getSouthWest().getLng(),
            },
            neLatLng: {
              lat: map.getBounds().getNorthEast().getLat(),
              lng: map.getBounds().getNorthEast().getLng(),
            },
        }))
        console.log(mapInfo);
    };

    //      
    useEffect(()=>{
        if(!mapInfo) return;
        if(mapInfo.level <= 6 && user.user_index) {
            axios({
                method: 'get',
                url: `/api/v1/community/main/${user.user_index}/?start=${mapInfo.neLatLng.lat},${mapInfo.neLatLng.lng}&end=${mapInfo.swLatLng.lat},${mapInfo.swLatLng.lng}`,
                data: {
                    header:{ "Authorization": refresh.refresh_token }
                }
            })
            .then((res)=>{
                setMarkerInfo(res.data.result);
            })
            .catch((error)=>{
                console.log(error);
            })
        } else { //  7    
            setMarkerInfo(null);
        }
    },[mapInfo])


    //  
    const [category, setCategory] = useState('');
    const categorySelect =(select) => { //   
        (category === select) ?  setCategory('') : setCategory(select)
    }


    //  open/close
    let [accOpen, setAccOpen] = useState([]);
    useEffect(()=>{
        if(!markerInfo) return;
        let tmp = [];
        for(let i=0; i<markerInfo.length; i++) { //         
            tmp.push({
                index: i, //  
                select: false, //true  open
            })
        }
        setAccOpen(tmp);

    },[markerInfo]);
    
    const handleAccOpen = (index) => {
        setAccOpen(accOpen.map( //   index   select  
            data => data.index === index 
                    ? {...data, select: !data.select} 
                    : {...data, select: false} 
        ))
    };

    
    return(
        <MainComponent
            state={state}
            markers={markers}
            info={info}
            accOpen={accOpen}
            markerInfo={markerInfo}
            setState={setState}
            setMap={setMap}
            setInfo={setInfo}
            handleMapInfo={handleMapInfo}
            handleAccOpen={handleAccOpen}
            categorySelect={categorySelect}
            category={category}
        ></MainComponent>
    );
}

export default MainPage;