import React, { useState } from "react";
import { Link } from "react-router-dom";
import UserInfoComponent from "./UserInfo";

import store from "../../../../Store/useStore";
import isLoginStore from "../../../../Store/isLoginStore";
import refreshTokenStore from "../../../../Store/refreshTokenStore";
import autoLoginStore from "../../../../Store/autoLoginStore";

const UserInfoWindow = () => {
  const state = store();
  const isState = isLoginStore();
  const refreshState = refreshTokenStore();
  const autoState = autoLoginStore();

  const [follow_count, set_follow] = useState(0);
  const [following_count, set_following_count] = useState(0);

  // open/close
  const [followInfo, setFollowInfo] = useState(false);
  function handlesetFollowInfo(e) {
    e.preventDefault();
    setFollowInfo(!followInfo);
  }

  // 
  const [followerSelect, setFollowerSelect] = useState(true);
  const [followingSelect, setFollowingSelect] = useState(false);
  function clickFollower(e) {
    e.preventDefault();
    setFollowerSelect(true);
    setFollowingSelect(false);
  }
  function clickFollowing(e) {
    e.preventDefault();
    setFollowerSelect(false);
    setFollowingSelect(true);
  }
  

  //  
  const onClickLogout = () => {
    state.set_access_token(null); // access  
    refreshState.set_refresh_token(null); // refresh  
    isState.nullIsLogin(); // null isLogin 
    autoState.nullAutoIsLogin();
    console.log(" ");
  };

  return (
    <>
      <UserInfoComponent
        state={state}
        follow_count={follow_count}
        following_count={following_count}
        followInfo={followInfo}
        followerSelect={followerSelect}
        followingSelect={followingSelect}
        onClickLogout={onClickLogout}
        handlesetFollowInfo={handlesetFollowInfo}
        clickFollower={clickFollower}
        clickFollowing={clickFollowing}
      ></UserInfoComponent>
    </>
  );
};

export default UserInfoWindow;
